@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body{font:1rem "Lato", sans-serif;color:#8e8d8f;padding:0;margin:0}a{color:#98c1d9;text-decoration:none}a:hover{opacity:0.5;text-decoration:underline}#page-wrapper{padding:1vh;background-color:#1d1e2c;min-height:100vh;width:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;box-sizing:border-box}#calculator{padding:1vh 0;display:flex;flex-direction:column;width:100%;max-width:600px}#formula,#display{text-align:right;padding:0 0.5rem}#display{background-color:#1d1e2c;border:0;color:#fffcf2;font-size:10vw}@media only screen and (min-width: 600px){#display{font-size:64px}}#formula{margin:0;min-height:1.25rem}#inputs{display:grid;padding:1vh 0;grid-gap:1vh}.input-group{display:grid;grid-gap:1vh}.input-group#special,.input-group#numbers{grid-template-columns:repeat(3, 1fr);grid-column:1 / span 3}.input-group#operators{grid-column:4 / 5;grid-row:1 / span 2}.key{font:1.6rem "Lato", sans-serif;border-radius:2px;padding:3vh;cursor:pointer;border:0;-webkit-transition:opacity 200ms ease-out;transition:opacity 200ms ease-out;-webkit-appearance:none}.key:hover{opacity:0.5}#special .key{color:#1d1e2c;background-color:#98c1d9}#numbers .key{color:#1d1e2c;background-color:#fffcf2}#operators .key{color:#fffcf2;background-color:#533a7b}#zero{grid-column:1 / span 2}

